import React from "react";
import { NavLink } from "react-router-dom";
import "./Links.scss";
import { paths } from "../appConfig";

const Links = () => {
  console.log(paths);

  const links = [
    { path: paths.aboutme.path, iconName: paths.aboutme.icon_name },
    { path: paths.skills.path, iconName: paths.skills.icon_name },
    { path: paths.projects.path, iconName: paths.projects.icon_name },
    { path: paths.education.path, iconName: paths.education.icon_name },
    { path: paths.contact.path, iconName: paths.contact.icon_name },
  ];

  return (
    <div className="LinksContainer">
      {links.map((l) => (
        <div className="LinkContainer_Inner" key={l.path.toString()}>
          <NavLink
            to={"/" + l.path}
            className="NavItem"
            activeClassName="NavItemActive"
          >
            <ion-icon name={l.iconName}></ion-icon>
          </NavLink>
        </div>
      ))}
    </div>
  );
};

export default Links;
