import React from "react";
import "./Me.scss";

import ReactTooltip from "react-tooltip";

const Me = () => {
  return (
    <div className="MeContainer">
      <h1>Dmitrijs Starcenko</h1>
      <div className="WARNING">
        <div>Under Development</div>
        <a data-tip data-for="devInfo">
          <ion-icon name="information-circle-sharp" id="devInfoIcon"></ion-icon>
        </a>

        <ReactTooltip place="bottom" id="devInfo">
          <p>Next:</p>
          <ul>
            <li>Create Tabs Component</li>
            <li>Show Off Working Station</li>
            <li>Show Off RPI NAS</li>
            <li>Gallery Fullscreen View</li>
            <li>Router Animations</li>
            <li>Caching</li>
            {/* <li>PWA Support</li> */}
          </ul>
          <p>Complete:</p>
          <ul>
            <li>Desktop Layout</li>
            <li>Create Gallery Component</li>
            <li>Add Work Projects Screenshots</li>
            <li>Mobile Layout</li>
            <li>Scaling Improvements</li>
            {/* <li>PWA Support</li> */}
          </ul>
        </ReactTooltip>
      </div>

      <span style={{ flexGrow: 1 }}></span>
      <div>
        <h2>Software Developer</h2>
        <h3>[FULL STACK]</h3>
      </div>
      <div style={{ display: "flex", flexDirection: "row" }}>
        <div className="NavItem">
          <a href="CV.pdf" download="CV.pdf">
            <ion-icon name="document"></ion-icon>
            <div className="Descr">CV</div>
          </a>
        </div>

        <div className="NavItem">
          <a
            href="https://www.linkedin.com/in/dlstarcenko"
            target="_blank"
            rel="noopener noreferrer"
          >
            <ion-icon name="logo-linkedin"></ion-icon>
          </a>
        </div>
        <div className="NavItem">
          <a href="./MasterThesis.pdf" download="./MasterThesis.pdf">
            <ion-icon name="document"></ion-icon>
            <div className="Descr">MT</div>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Me;
