import React from "react";
import "./ProjectsScreen.scss";
import PageHeader from "../Components/PageHeader";
import ProjectDisplay, { constants } from "../Components/ProjectDisplay";
import { paths } from "../appConfig";

const ProjectsScreen = () => {
  return (
    <>
      <PageHeader header="PROJECTS" iconName={paths.projects.icon_name}>
        {/* Several projects completed which have visual front end */}
      </PageHeader>

      {/* <h3>Mobile Projects:</h3> */}

      <div className="ProjectsContainer">
        <ProjectDisplay
          projectType={constants.MOBILE}
          orientation={constants.PORTRAIT}
          projectName="Insights Discovery"
          numOfImages={42}
        ></ProjectDisplay>

        <ProjectDisplay
          projectType={constants.MOBILE}
          orientation={constants.PORTRAIT}
          projectName="Insights Marketing"
          numOfImages={30}
        ></ProjectDisplay>
      </div>

      <div className="Divider"></div>

      <div className="ProjectsContainer">
        {/* <h3>Web Projects:</h3> */}
        <ProjectDisplay
          projectType={constants.WEB}
          orientation={constants.LANDSCAPE}
          projectName="Team Wheel"
          numOfImages={8}
        ></ProjectDisplay>
      </div>
    </>
  );
};

export default ProjectsScreen;
