import React from "react";
import "./EducationList.scss";

const EducationList = ({ subject, degree, year, modules }) => {
  return (
    <div className="EducationListContainer">
      <div className="Education_Headers_Container">
        <div className="Education_Header_Container">
          <div className="Education_Header_1">Subject:</div>
          <div className="Education_Header_1">Degree:</div>
          <div className="Education_Header_1">Year:</div>
        </div>

        <div className="Education_Header_Container">
          <div className="Education_Header_2">{subject}</div>
          <div className="Education_Header_2">{degree}</div>
          <div className="Education_Header_2">{year}</div>
        </div>
      </div>
      <ul className="EducationList_Modules">
        {modules.map((m) => (
          <li key={m.toString()}>{m}</li>
        ))}
      </ul>
    </div>
  );
};

export default EducationList;
