import React from "react";
import "./SkillList.scss";

const SkillList = ({ header, skills }) => {
  return (
    <div className="SkillListContainer">
      <h3 className="SkillListHeader">{header}</h3>
      <ul>
        {skills.map((s) => (
          <li key={s.toString()}>{s}</li>
        ))}
      </ul>
    </div>
  );
};

export default SkillList;
