import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import AboutMeScreen from "../Screens/AboutMeScreen";
import ProjectsScreen from "../Screens/ProjectsScreen";
import ContactScreen from "../Screens/ContactScreen";
import EducationScreen from "../Screens/EducationScreen";
import SkillsScreen from "../Screens/SkillsScreen";

import { paths } from "../appConfig";

const Routes = () => {
  return (
    <Switch>
      <Route path={`/${paths.aboutme.path}`} component={AboutMeScreen} />
      <Route path={`/${paths.skills.path}`} component={SkillsScreen} />
      <Route path={`/${paths.projects.path}`} component={ProjectsScreen} />
      <Route path={`/${paths.contact.path}`} component={ContactScreen} />
      <Route path={`/${paths.education.path}`} component={EducationScreen} />
      <Route path="*">
        <Redirect to={`/${paths.skills.path}`} />
      </Route>
    </Switch>
  );
};

export default Routes;
