import React from "react";
import "./EducationScreen.scss";
import PageHeader from "../Components/PageHeader";
import EducationList from "../Components/EducationList";
import { paths } from "../appConfig";

const EducationScreen = () => {
  const education = [
    {
      subject: "Cyber Security",
      degree: "Masters(2:1)",
      year: "2017-2020",
      modules: [
        "Secure Programming for Web",
        "Secure Programming for Apps",
        "Network Security and Pen Testing",
        "Cloud Security",
        "Cryptography",
        "Malware Analysis",
        "IT Law and Ethics",
        "Research in Computing",
        "Security Fundamentals",
      ],
    },
    {
      subject: "Games Development",
      degree: "Bachelors (1:1)",
      year: "2009-2013",
      modules: [
        "Programming Principles",
        "OOP (C++ | Java | C#)",
        "DataBase Systems",
        "Web Programming",
        "Rendering Algorythms",
        "Maths",
        "Calculus",
        "Game Physics",
        "2D Game Engine Development",
        "3D Game Development",
        "A.I.",
        "3D Modeling",
        "3D Animation",
        "Networking",
        "Sound Technology",
        "Games Theory and Practice",
        "GUI Design",
        "Operating Systems",
      ],
    },
  ];

  return (
    <>
      <PageHeader
        header="EDUCATION"
        iconName={paths.education.icon_name}
      ></PageHeader>
      <div className="EducationListsContainer">
        {education.map((e) => (
          <EducationList
            key={e.subject.toString()}
            subject={e.subject}
            degree={e.degree}
            year={e.year}
            modules={e.modules}
          />
        ))}
      </div>
    </>
  );
};

export default EducationScreen;
