import React from "react";
import "./SkillsScreen.scss";
import PageHeader from "../Components/PageHeader";
import SkillList from "../Components/SkillList";
import { paths } from "../appConfig";

const SkillsScreen = () => {
  const myPrimaryTechnicalSkills = [
    { header: "OOP / Scripting", skills: ["JavaScript", "Python", "C#"] },
    { header: "BackEnd Dev", skills: ["Node", "Flask", "ASP.NET"] },
    {
      header: "FrontEnd Dev",
      skills: ["React", "Angular 2+", "ASP.NET MVC", "HTML | S(CSS) | JS"],
    },
    {
      header: "State Management",
      skills: ["Redux", "Redux Toolkit", "React Query"],
    },
    {
      header: "Databases + ORM",
      skills: ["MSQL + EF", "Mongo + Mongoose"],
    },
    { header: "Mobile Dev", skills: ["React Native / Expo", "Appcelerator"] },
    { header: "Source Control", skills: ["Git", "GitLab", "TFS"] },
    { header: "Testing", skills: ["Jest", "Mocha + Chai", "NUnit"] },
    { header: "Contenerization", skills: ["Docker"] },
    // { header: "Package Managers", skills: ["NPM", "Nuget"] },
  ];

  const mySecondaryTechnicalSkills = [
    {
      header: "Image Editing / Design",
      skills: ["GIMP", "Adobe Photoshop", "Adobe XD"],
    },
    { header: "Game Engine", skills: ["Unity 3D"] },
    { header: "3D Modeling", skills: ["Blender"] },
  ];

  const myOtherActivities = [
    { header: "Sport Activities", skills: ["Calisthenics", "Weight Lifting"] },
    { header: "Gaming (PC)", skills: ["Path Of Exile", "Witcher 3", "Wolcen"] },
    { header: "Home Automation", skills: ["Raspberry PI", "HASS IO"] },
  ];
  return (
    <>
      <PageHeader header="SKILLS" iconName={paths.skills.icon_name}>
        {/* <article>
          Highly focused software developer with over five years experience in a
          variety of development positions. Organised, methodical and a keen eye
          for detail results in solid coding and trustworthy software programmes
        </article> */}
      </PageHeader>

      <div>
        <h4>Primary Technical Skills:</h4>
        <div className="TechincalSkillsContainer">
          {myPrimaryTechnicalSkills.map((s) => (
            <SkillList
              key={s.header.toString()}
              header={s.header}
              skills={s.skills}
            />
          ))}
        </div>
      </div>

      <div className="Divider"></div>

      <div>
        <h4>Secondary Technical Skills:</h4>
        <div className="TechincalSkillsContainer">
          {mySecondaryTechnicalSkills.map((s) => (
            <SkillList
              key={s.header.toString()}
              header={s.header}
              skills={s.skills}
            />
          ))}
        </div>
      </div>

      <div className="Divider"></div>

      <div>
        <h4>Other Activities:</h4>
        <div className="TechincalSkillsContainer">
          {myOtherActivities.map((s) => (
            <SkillList
              key={s.header.toString()}
              header={s.header}
              skills={s.skills}
            />
          ))}
        </div>
      </div>
    </>
  );
};

export default SkillsScreen;
