import React from "react";
import PageHeader from "../Components/PageHeader";
import "./ContactScreen.scss";
import { paths } from "../appConfig";

const ContactScreen = () => {
  return (
    <>
      <PageHeader
        header="CONTACT"
        iconName={paths.contact.icon_name}
      ></PageHeader>
      <div id="map">
        <div id="maploader">
          <div>MAP LOADING...</div>
        </div>
        <iframe
          title="LocationGoogleMap"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d152515.98632965787!2d-6.385784896089965!3d53.32423806922109!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x48670e80ea27ac2f%3A0xa00c7a9973171a0!2sDublin!5e0!3m2!1sen!2sie!4v1564509835552!5m2!1sen!2sie"
          allowFullScreen
        ></iframe>
      </div>
      <div className="ContactDetails">
        <ion-icon name="compass"></ion-icon>
        <p>Ireland - Dublin</p>
      </div>
      <div className="ContactDetails">
        <ion-icon name="mail"></ion-icon>
        <p>dlstarcenko@gmail.com</p>
      </div>
    </>
  );
};

export default ContactScreen;
